.app {
  font-family: "Sarabun", sans-serif;
  display: -webkit-flex;
  display: flex;
  margin: auto;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}
.no-location {
  display: -webkit-flex;
  display: flex;
  margin: auto;
  height: 300px;
  width: 500px;
  background-color: rgb(238, 238, 238);
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  font-size: 2em;
  text-align: center;
  border-radius: 5px;
  opacity: 0.9;
}
.container {
  opacity: 0.9;
  background-color: rgb(238, 238, 238);
  height: 45vh;
  width: 60vw;
  border-radius: 5px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  margin: auto;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.photo-credit {
  -webkit-align-self: flex-end;
          align-self: flex-end;
  margin: 0 20px 0 10px;
  font-style: italic;
}

a {
  color: black;
}
.location-time {
  width: 80%;
  text-align: center;
  height: 130px;
  font-size: 1.8em;
}
.location {
  font-size: 1.2em;
  margin-bottom: 15px;
}
.time {
  font-size: 0.9em;
}
.forecast {
  display: -webkit-flex;
  display: flex;
  width: 90%;
  text-align: center;
  height: 200px;
}
.day {
  font-size: 1.2em;
  text-align: center;
  width: 20%;
}
.day-date {
  font-size: 1.2em;
}
.weather-icon {
  margin: auto;
}
.weather-icon img {
  height: 70px;
}
.current-temp {
  font-size: 2em;
  margin-top: -12px;
}
.min-max {
  width: 70%;
  margin: auto;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
}

@media (max-width: 1300px) {
  .day {
    font-size: 1.1em;
  }
  .location-time {
    font-size: 1.7em;
  }
  .min-max {
    width: 80%;
  }
}

@media (max-width: 1100px) {
  .day {
    font-size: 1em;
  }
  .location-time {
    padding-top: 10px;
    font-size: 1.6em;
  }
}

@media (max-width: 950px) {
  .container {
    height: 50vh;
    width: 70vw;
  }
}

@media (max-width: 800px) {
  .container {
    height: 50vh;
    width: 80vw;
  }
}

@media (max-width: 675px) {
  .container {
    height: 50vh;
    width: 100vw;
  }
}

@media (max-width: 550px) {
  .container {
    height: 40vh;
  }
  .day {
    font-size: 0.9em;
  }
  .weather-icon img {
    height: 50px;
  }
}
@media (max-width: 500px) {
  .container {
    height: 40vh;
  }
  .day {
    font-size: 0.8em;
  }
  .weather-icon img {
    height: 50px;
  }
}
@media (max-width: 450px) {
  .container {
    height: 40vh;
  }
  .location-time {
    font-size: 1.5em;
  }
  .location {
    margin-bottom: 0;
  }
  .day {
    font-size: 0.7em;
  }
  .weather-icon img {
    height: 40px;
  }
  .min-max {
    width: 95%;
  }
}

